.case {
  font-style: normal;
  font-weight: normal;
  background-color: @special-bright-grey;
}

.case__wrapper {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.case__title {
  font-family: "Oswald", sans-serif;
  font-weight: inherit;
  font-size: 36px;
  line-height: 40px;
  color: @basic-black;
  padding-top: 27px;
}

.case__description {
  font-family: "Arial", sans-serif;
  font-weight: inherit;
  font-size: 14px;
  line-height: 18px;
  color: @special_darker-darkgrey;
}

.case__list {
  list-style: none;
}

.case__value {
  font-family: "Oswald", sans-serif;
  font-weight: inherit;
  font-size: 24px;
  line-height: 24px;
  color: @basic-black;
}

.case__legend {
  font-family: "Arial", sans-serif;
  font-weight: inherit;
  font-size: 12px;
  line-height: 12px;
  color: @special_darker-darkgrey;
}

.case__total {
  font-family: "Oswald", sans-serif;
  font-weight: inherit;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: @basic-black;
}
